body {
    background-color: #f7f7f7;
    font-family: 'Poppins', sans-serif;
}

.uk-input,
.uk-button,
p,
a,
.uk-search-input,
td {
    font-family: 'Poppins', sans-serif !important;
}

.web-cart>span {
    color: #d43b0c;
}

.web-cart>span.uk-badge {
    color: white;
    background-color: #d43b0c;
    font-size: 7pt;
    padding: 2px !important;
    margin-left: 4px;
}

.mobile-searchicon {
    display: none;
}

.mobile-searchicon>li>a>span {
    color: #d43b0c;
}

.home-offer-story {
    display: none;
}

.home-offer-story>div>a>img {
    height: 55px;
    border-radius: 50%;
    width: 55px;
    object-fit: cover;
}

.primary-modal-nav {
    background-color: #d43b0c !important;
    display: none;
}

.primary-modal-nav>div>ul>li>a {
    color: white !important;
    text-transform: none;
    font-size: 12pt;
}

.primary-modal-nav>div>ul>li>a:hover {
    color: white;
}

.primary-modal-nav.uk-navbar-item,
.primary-modal-nav>div>.uk-navbar-nav>li>a {
    min-height: 60px;
}

.login-modal {
    background-color: #f7f7f7;
    border-radius: 6px;
}

.login-modal>.uk-modal-header {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.login-modal-title {
    font-size: 11pt;
    color: black;
    font-weight: 500;
    text-align: center;
}

.login-modal-subtitle {
    font-size: 12pt;
    color: black;
    text-align: center;
}

.login-input-phone {
    font-size: 11pt;
    color: black !important;
    height: 44px;
    border: 1px solid #444444;
    border-radius: 4px;
}

.login-input-phone:focus {
    border: 1px solid #444444;
}

.login-input-phone::placeholder {
    font-size: 10pt !important;
    color: #666 !important;
}

.login-input-btn {
    background-color: #d43b0c;
    color: white !important;
    padding: 2px;
    border-radius: 6px !important;
}

.login-input-btn:hover {
    color: white;
}

.login-terms {
    font-size: 10pt;
    color: #444444;
    text-align: center;
}

.login-terms>a {
    text-decoration: underline;
}

.mob-categories-grid {
    margin-bottom: 2px;
    margin-top: 2px !important;
}

.mob-categories-heading {
    font-size: 12pt;
    background-size: 100%;
    background-repeat: no-repeat;
    color: black !important;
    font-weight: 600;
    background-position: center;
    text-transform: uppercase;
}

.mob-categories-heading>span {
    font-size: 9pt;
    color: #444;
    font-weight: 500;
    text-transform: none !important;
}

.category-subnav>li>a {
    padding: 10px;
    color: black;
    font-size: 10pt;
}

.category-subnav>li>a:hover,
.category-subnav>li>a:active {
    color: #d43b0c;
}

.mob-categories-grid.uk-open>a {
    border-left: 3px solid #d43b0c;
}

.product-modal-grid {
    border-radius: 6px;
    overflow: auto;
    margin: 20px;
    width: 75%;
}

.pm-product-image {
    width: 100% !important;
    height: 400px;
    object-fit: cover;
}

.pm-product-badge {
    background-color: #51aa1b;
    color: white;
    border-radius: 4px;
    padding: 14px 10px;
    margin-bottom: 10px !important;
}

.pm-product-name {
    font-size: 13pt;
    color: black;
    font-weight: 500;
}

.pm-product-category {
    font-size: 10pt;
    color: black !important;
}

.pm-product-price {
    font-size: 12pt;
    color: black;
    margin-top: 6px !important;
}

.pm-product-price>span {
    font-size: 17pt;
    color: black;
    font-weight: 600;
    font-family: 'Roboto', sans-serif !important;
}

.pm-product-mrp {
    margin-top: 5px !important;
    color: #444444;
}

.pm-product-mrp>span {
    font-size: 14pt;
    font-family: 'Roboto', sans-serif !important;
    text-decoration: line-through;
}

.pm-product-taxw {
    font-size: 9pt;
    color: #666;
}

.pm-avail-heading {
    font-size: 10pt;
    color: black;
}

.pm-variant-button>button {
    padding: 2px 15px;
    color: #444444;
    border: 1px solid #444;
}

.pm-desc-heading {
    font-size: 12pt;
    color: #444444;
}

.pm-product-description {
    font-size: 10pt;
    color: #444444;
}

.pm-details-grid {
    padding: 20px;
}

.product-variant {
    background-color: white;
    border-radius: 6px;
}

.variant-product-name {
    font-size: 11pt;
    color: black;
}

.variant-details-button {
    font-size: 10pt;
    text-transform: capitalize;
    color: #d43b0c;
}

.variant-qty {
    font-size: 14pt;
    font-weight: 600;
    color: #444444;
    font-family: 'Poppins', sans-serif;
    text-align: left;
}

.variant-qty>span {
    font-size: 10pt;
}

.variant-price {
    font-size: 14pt;
    color: black;
    text-align: left;
    margin: 0px;
    font-weight: 600;
    font-family: 'Roboto', sans-serif !important;
}

.variant-set {
    text-align: left;
}

.variant-mrp {
    font-size: 10pt !important;
    font-family: 'Roboto', sans-serif !important;
    text-decoration: line-through;
    color: #666;
    font-weight: 600;
}

.variant-info-grid {
    background-color: white !important;
    margin-bottom: 4px !important;
    border-bottom: 1px solid #d43b0c;
}

.web-category-grid {
    background-color: white;
    max-height: 100vh;
    border-right: 2px solid #f7f7f7;
    overflow: auto;
}

.web-category-grid::-webkit-scrollbar {
    width: 8px;
}


/* Track */

.web-category-grid::-webkit-scrollbar-track {
    background: #f1f1f1;
}


/* Handle */

.web-category-grid::-webkit-scrollbar-thumb {
    background: #888;
}


/* Handle on hover */

.web-category-grid::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.web-category-products {
    background-color: white;
    height: calc(100vh - 80px);
}

.web-category-breadcrumb>li>a {
    font-size: 9pt;
}

.category-query-heading {
    font-size: 12pt;
    color: black;
    text-align: left;
    margin-top: 4px;
    font-weight: 500;
}

.mobile-search-bar {
    padding: 1px 10px !important;
    border-radius: 6px;
    width: 100%;
    background-color: #f7f7f7 !important;
}

.mobile-search-bar::placeholder {
    font-size: 10pt;
    color: #666;
}

.search-nav {
    background-color: white !important;
}

.search-modal {
    background-color: #f7f7f7;
}

.user-name {
    font-size: 12pt;
    text-align: left;
    color: black;
}

.user-info-grid {
    background-color: #f7f7f7;
    border-radius: 6px;
    border-left: 4px solid #009578;
}

.user-phone {
    font-size: 9pt;
    color: #444;
    text-align: left;
}

.user-email {
    font-size: 10pt;
    color: #666;
    text-align: left;
}

.mobile-profile>li {
    padding: 10px;
    background-color: #f7f7f7;
    margin-bottom: 4px;
    border-radius: 6px;
}

.mobile-profile>li>a {
    color: white;
    font-size: 10pt;
}

.mobile-profile>li>a>span:first-child {
    margin-right: 20px;
}

@media only screen and (max-width: 1300px) {
    .web-search-bar {
        width: 300px;
    }
    .navbar {
        padding-left: 10px;
        padding-right: 10px;
    }
}

@media only screen and (max-width: 920px) {
    .web-menu,
    .web-search,
    .navbar-location {
        display: none;
    }
    .navbar {
        padding-left: 10px;
        padding-right: 10px;
    }
    .uk-logo.logo-lg {
        width: 120px;
    }
    .mobile-searchicon {
        display: flex;
    }
    .main-homebanner {
        height: 150px;
    }
    .home-offer-story {
        display: flex;
    }
    .mobile-nav {
        display: flex;
    }
    .home-section-title {
        font-size: 12pt;
    }
    .product-img {
        width: 100%;
    }
    .home-category-title {
        font-size: 11pt;
    }
    .slider-side-category {
        display: none;
    }
    .home-category-grid>div>img {
        max-height: 84px !important;
    }
    .web-second-banner {
        height: 200px;
        border-radius: 6px;
    }
    .footer-container {
        display: none;
    }
    .price-variant {
        background-color: #d43b0c;
        color: white;
        border: none;
    }
    .price-variant:hover {
        background: none;
        color: #d43b0c;
        border: 1px solid #d43b0c;
    }
    .uk-modal-body.offer-modal {
        min-height: 100vh;
        border-radius: 0px;
        padding: 0px;
    }
    #offer-modal.uk-modal,
    #login-modal.uk-modal,
    #otp-modal.uk-modal,
    #categories-modal.uk-modal,
    #product-modal.uk-modal,
    #search-category.uk-modal,
    #orders-modal.uk-modal,
    #wallet-modal.uk-modal,
    #wallet-history.uk-modal,
    #wallet-history.uk-modal,
    #redeem-modal.uk-modal,
    #address-modal.uk-modal,
    #add-address.uk-modal,
    #order-details.uk-modal {
        padding-top: 0px !important;
    }
    #orders-modal.uk-modal>div>button.uk-modal-close-default,
    #offer-modal.uk-modal>div>button.uk-modal-close-default,
    #wallet-modal>div>button.uk-modal-close-default,
    #wallet-history>div>button.uk-modal-close-default,
    #redeem-modal>div>button.uk-modal-close-default,
    #address-modal>div>button.uk-modal-close-default,
    #add-address>div>button.uk-modal-close-default,
    #order-details>div>button.uk-modal-close-default {
        display: none;
    }
    .primary-modal-nav {
        display: flex;
    }
    .login-modal>.uk-modal-header {
        display: none;
    }
    .login-modal {
        border-radius: 0px;
        height: 100vh;
    }
    .cart-item-grid {
        height: 65vh !important;
    }
    .cart-payment-grid {
        height: auto !important;
    }
    .cart-product-price {
        margin-left: 5px;
    }
    .cart-product-price>span {
        margin-right: 5px;
    }
    .cart-product-image {
        width: 98%;
    }
    .product-modal-grid {
        width: 100%;
        border-radius: 0px;
        margin: 0px;
    }
    .pm-details-grid {
        padding: 5px;
        padding-top: 10px;
        padding-bottom: 15%;
    }
    .product-variant {
        padding: 0px;
    }
    .variant-product-img {
        width: 50%;
    }
    .web-category-grid {
        display: none;
    }
    #search-category>div>button.uk-modal-close-default,
    #search-category>div>div.uk-modal-header {
        display: none;
    }
    .orders-modal {
        border-radius: 0px;
    }
    .orders-modal,
    .wallet-modal,
    .wallet-history,
    .redeem-modal,
    .address-modal,
    .add-address-modal,
    .order-details {
        padding: 0px;
        border-radius: 0px;
        height: 95vh;
    }
    .wallet-info-grid {
        border-radius: 0px;
    }
    p.wallet-title {
        margin-top: 20px !important;
    }
    .wallet-info-grid {
        background-color: white !important;
    }
    .redeem-input {
        border-bottom: 1px solid #444444 !important;
    }
    .address-grid {
        background-color: #f7f7f7;
    }
    ol.progtrckr li.progtrckr-done {
        font-size: 8pt;
    }
    .my-product-name {
        font-size: 9pt;
    }
    .my-amount-grid {
        border-radius: 0px;
    }
}